.header,
.mainHeader {
  position: sticky;
  top: 0;
  height: 82em;
  background: var(--bg-green-color);
  &__content {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: sticky;
    top: 0;
    box-sizing: border-box;
  }
  .topColumn {
    position: sticky;
  }
  .search {
    z-index: 4;
    position: sticky;
    top: 15em;
  }

  .topColumn {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .title {
    z-index: 1;
    width: 40%;
    font-size: 40em;
    font-weight: 700;
    color: white;
    text-align: center;
    margin-bottom: 1em;
  }
  .description {
    z-index: 1;
    color: white;
    font-size: 20em;
    margin-bottom: 3em;
  }
}
.mainHeader {
  top: -408px;
  height: 490px;
}
.search {
  display: none;
  width: 45%;
}
.gradientMaskInactive,
.gradientMask {
  width: 100%;
  height: 100%;
  position: sticky;
  top: 0;
}
.gradientMaskInactive {
  display: flex;
  align-items: center;
}
.gradientMask {
  height: 182px;
  width: 100%;
  z-index: 2;
  background: linear-gradient(
    to bottom,
    rgba(36, 83, 83, 1),
    rgba(36, 83, 83, 0.99),
    rgba(36, 83, 83, 0.95),
    rgba(36, 83, 83, 0.9),
    rgba(36, 83, 83, 0.8),
    rgba(36, 83, 83, 0.7),
    rgba(36, 83, 83, 0.6),
    rgba(36, 83, 83, 0.58),
    rgba(36, 83, 83, 0.55),
    rgba(36, 83, 83, 0.4),
    rgba(36, 83, 83, 0.2),
    rgba(36, 83, 83, 0)
  );
}

.mainSearchBottom,
.searchTop {
  display: block;
}
.mainSearchBottom,
.searchBottom {
  margin-bottom: 17em;
}
@media (orientation: landscape) {
  .topColumn {
    top: 17em;
  }
}
@media (orientation: portrait) {
  .topColumn {
    top: 3em;
  }
  .search {
    width: 100%;
  }
  .searchTop {
    display: none;
  }
  .searchBottom {
    display: block;
  }
  .mainHeader {
    top: -480em;
  }

  .header {
    height: 132em;
  }
  .title {
    min-width: 80%;
  }
}
